@import 'global.scss';

:host {
  /**
   * @prop --dropdown-background-color: The background color of the dropdown.
   * @prop --dropdown-border: The border style of the dropdown.
   * @prop --dropdown-border-radius: The border radius of the dropdown.
   * @prop --dropdown-box-shadow: The box shadow of the dropdown.
   * @prop --dropdown-max-width: The maximum width of the dropdown wrapper.
   * @prop --dropdown-container-padding: The padding of the dropdown inner container.
   * 
   * @prop --dropdown-wrapper-transition: The transition of the dropdown wrapper.
   * 
   * @prop --dropdown-container-margin: The margin of the outer container of the dropdown.
   * @prop --dropdown-container-padding: The padding of the inner container of the dropdown.
   *
   * @prop --dropdown-item-padding: The padding of the item inside the unnamed slot of the dropdown.
   * @prop --dropdown-item-background-color: The background color of the item inside the unnamed slot of the dropdown.
   * @prop --dropdown-item-hover-background-color: The background color of the item inside the unnamed slot of the dropdown on hover.
   *
   * @prop --dropdown-animation-duration: The duration of the opening and closing animations of the dropdown.
   *
   * @prop display: The display mode of the host element.
   * @prop position: The position of the host element.
   */
  --dropdown-background-color: #{$rwc-color-foundation-white};
  --dropdown-border: 1px solid #{$rwc-color-border-primary};
  --dropdown-border-radius: #{$rwc-border-radius--md};
  --dropdown-box-shadow: #{$rwc-box-shadow-lg};
  --dropdown-max-width: 100%;

  --dropdown-wrapper-transition: height var(--dropdown-animation-duration) ease-in-out;
  
  --dropdown-container-margin: #{$rwc-spacing--3xs} 0 0 0;
  --dropdown-container-padding: #{$rwc-spacing--xs} 0;
  
  --dropdown-item-padding: #{$rwc-spacing--3xs} #{$rwc-spacing--xs};
  --dropdown-item-background-color: transparent;
  --dropdown-item-hover-background-color: #{$rwc-color-grey-200};
  
  --dropdown-animation-duration: 200ms;

  display: block;
  position: relative;
}

.wrapper {
  background-color: var(--dropdown-background-color);
  border: var(--dropdown-border);
  border-radius: var(--dropdown-border-radius);
  box-shadow: var(--dropdown-box-shadow);
  height: 0;
  left: #{$off-screen};
  margin: var(--dropdown-container-margin);
  max-width: var(--dropdown-wrapper-max-width);
  overflow: hidden;
  position: absolute;
  transition: visibility 0ms var(--dropdown-animation-duration), left 0ms var(--dropdown-animation-duration), var(--dropdown-wrapper-transition);
  
  &.open {
    left: 0;
    visibility: visible;
    height: auto;
    transition: var(--dropdown-wrapper-transition);
  }
  
  &.mode-fixed {
    position: fixed;
  }
  
  &.size-auto {
    .container {
      display: inline-block;
    }
  }
}

.container {
  padding: var(--dropdown-container-padding);
}

::slotted(*) {
  background-color: var(--dropdown-item-background-color);
  padding: var(--dropdown-item-padding);
}

::slotted(*:hover) {
  background-color: var(--dropdown-item-hover-background-color);
}
